import React from "react";
import quotes from "../data/quotes";

const nth = function (d) {
  if (d > 3 && d < 21) return "th";
  switch (d % 10) {
    case 1:
      return "st";
    case 2:
      return "nd";
    case 3:
      return "rd";
    default:
      return "th";
  }
};


const oneDay = 24 * 60 * 60 * 1000
const months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

class QuoteOfTheDay extends React.Component {
  state = { time: Date.now() }

  componentDidMount() {
    this.interval = setInterval(
      () => this.setState({ time: Date.now() }),
      1000
    );
  }
  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    /*
    console.log(oneDay - this.state.now.getMilliseconds());
    const start = new Date(this.state.now.getFullYear(), 0, 0);
    const diff =
      this.state.now -
      start +
      (start.getTimezoneOffset() - this.state.now.getTimezoneOffset()) *
        60 *
        1000;
    const day = Math.floor(diff / oneDay);
    const quote = quotes[Math.floor((day / 366) * quotes.length)];
    */
    return (
      <>
        <div className="h-screen flex flex-col justify-center items-center bg-yellow-300">
          <h1 className="text-9xl uppercase font-black">Quote Of The Day</h1>
          {/*
          <p>
            {months[this.state.now.getMonth()]}{" "}
            {this.state.now.getDate() + nth(this.state.now.getDate())}
          </p>*/}
        </div>
        <div> {this.state.time} </div>
        {/*
        <button
          className="py-6 px-10 bg-blue-500 rounded-full text-3xl hover:bg-blue-400 active:bg-blue-300 transition duration-300 ease-in-out flex items-center animate-bounce"
          onClick={() => this.state.now.setDate(this.state.now.getDate() + 1)}
        >
          Add a day
        </button>
        <div className="card text-2xl mx-48">
          <p className="card-text">{quote.text}</p>
          <h5 className="card-title">
            {quote.author.username}{" "}
            <i className={`fab fa-${quote.socialMedia} fa-lg`}></i>
          </h5>
        </div>*/}
      </>
    );
  }
}

export default QuoteOfTheDay;
